@font-face{font-family:RobotoSlab;src:url(../../fonts/RobotoSlab/RobotoSlab-Regular.ttf)}:root{--indigo: #3d0088}body{margin:0}.main-layout{width:1440px;display:grid;grid-template-columns:160px 458px 27px 649px 146px;grid-template-rows:197px 1fr 197px;background-image:radial-gradient(circle at 100% 0, var(--indigo), #220045 99%);margin:70px auto 0px auto}.color-picker-container{grid-column:2/3;grid-row:2/3;display:grid;grid-template-columns:21px 6px 100px 12px 292px 10px 17px;grid-template-rows:27px 404px 13px 46px 21px 102px 17px;background-color:#d8d8d8}.color-picker-container .bitmap{grid-column:3/6;grid-row:2/3;background:linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, black 100%),linear-gradient(to right, white 0%, #5500ff 100%);cursor:crosshair}.color-picker-container .rectangle-color{grid-column:3/4;grid-row:4/5;background-color:#86c}.color-picker-container .hsl{grid-column:5/6;grid-row:4/5;padding:5px 57px 6px 10px;font-family:RobotoSlab;font-size:27px;font-weight:normal;font-stretch:normal;font-style:normal;line-height:normal;letter-spacing:normal;color:#000}.color-picker-container .btn-preview::first-letter{text-transform:capitalize}.color-picker-container .btn-preview{display:flex;justify-content:space-around;align-items:center;grid-column:2/7;grid-row:6/7;color:#fff;background:radial-gradient(circle at 100% 0, var(--indigo), #220045 99%);border-radius:10px;border:5px solid #fff;outline:var(--indigo) solid 5px;font-size:30px;font-family:RobotoSlab;cursor:pointer;text-transform:lowercase}.color-picker-container .btn-preview::before{content:"⬢";color:#fff;font-size:50px}.color-picker-container .btn-preview::after{content:"⬢";color:#fff;font-size:50px}.preview-container{grid-column:4/5;grid-row:2/3;background-color:#d8d8d8;display:grid;grid-template-columns:27px 65px 172px 122px 172px 64px 27px;grid-template-rows:27px 68px 93px 139px 207px 69px 27px}.preview-container .checkerboard-svg{grid-column:2/-2;grid-row:2/-2;background-image:url("../../assets/imgs/checkerboard.svg")}.preview-container .shirt-svg{grid-column:3/-3;grid-row:3/-3;background-image:url("../../assets/imgs/shirt.svg")}.preview-container .logo-svg{grid-column:4/5;grid-row:4/5;width:122px;height:139px}